import React, { useState, useEffect } from 'react';
import style from './nav.css';
import ReactDOM from 'react-dom';


const Nav = () => {

        
  const [show, setShow]=useState(false);

  const toggleClass = () => {
        setShow(!show)
  }

    return (
        <div className="main-nav">
            <div className="nav">
                <h1 className="logo">Movies n-Stuff</h1>
            
           
                <a className="link" href="#">Home</a>
                <a className="link" href="#">About Us</a>
                <a className="link" href="#">Forum</a>
                <div>
                    <button className="link btn">Log In</button>
                </div>
            
            <div onClick={toggleClass} className="hamburger">
                <div  className={show ? "active-1 active-ham lines line1" : "lines inactive-ham inactive-1 line1"}></div>
                <div  className={show ? "active-2 active-ham lines line2" : "lines inactive-ham inactive-2 line2"}></div>
            </div>
            </div>
            

                 
                       <div onClick={toggleClass} className={show ? "active menu" : "inactive menu"}> 
                        <div className="home item">
                            <a href="#">Home</a>
                        </div>

                        <div className="about item">
                             <a href="#">About Us</a>
                        </div>

                        <div className="forum item">
                             <a href="#">Forum</a>
                        </div>

                        <div className="login item">
                            <a href="#">Log in</a>
                        </div>
                    </div>
             
          
           
            
        </div>
       
    )
}






export default Nav;