import React from 'react';
import style from './left_one.css'; 





const Left_one = ({item}) => {
    return (
    <div className="left-one">
      <div className="img-div">
        <img src={`https://image.tmdb.org/t/p/w185_and_h278_bestv2/${item.poster_path}`} alt="movie-img"/>
      </div>
      
    </div>
    )
}



export default Left_one;