import React, {useState, useEffect} from 'react';
import style from './section_one.css'; 

import Left_one from './Left_one';
import Section_two from './Section_two';
import { BsFillCameraVideoFill } from "react-icons/bs";




const Section_one = () => {

    
  const API_KEY = '54cac1d9664e47076b1d592574914520';

  const [movies, setMovies] = useState([]);
  const [search, setSearch] = useState('');
  const [query, setQuery] = useState('Jurrasic park');


  useEffect(() => {
    getMovies();
}, [query]);



  const getMovies = async () => {
    const response = await fetch(`https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&query=${query}`);
    const data = await response.json();
    const newData = data.results; 
    const wholeData = newData.map(item => [item]); //turned the array object into an array
    setMovies(wholeData[1]);

   
}

const getSearch = e => {
  e.preventDefault();
  setQuery(search) //query == search
  setSearch('');
}

const updateSearch = e => {
      setSearch(e.target.value)
}


console.log(movies[1]);


return (
    <div className="section">
        <div className="section-one">

            <div className="left-one">
                 {movies.filter(item => item.poster_path).map(item => 
                     <Left_one  
                         key={item.id}
                         item={item}
                        />
                    )}
            <div/>
        
           </div>

            <div className="right-one">
                <h2>Search for a movie</h2>
                <div className="search-query">
                    <div className="input">
                            <div className="camera">< BsFillCameraVideoFill /></div>
                            <input placeholder="Search" value={search} onChange={updateSearch}></input>
                    </div>
                    <button onClick={getSearch}>Enter</button>
                </div>
                
            </div>
        </div>

        
  
  

        

       
        <div>
            {movies.map(item => (   
                <Section_two 
                    className="section-two" 
                    key={item.id}
                    item={item}

   
                 />
             ))} 
        </div>
    
    </div>
    
  
    )}



export default Section_one;

 