import React from 'react';
import style from './section_two.css'; 
import ReactDOM from 'react-dom';




const Section_two = ({item}) => {
    return (
    <div className="section-two">
       
       <div className="left-two">
       <h1>{item.title}</h1>
      <div className="section-div">
         <div className="release-date title">Release Date:</div> 
         <div className="title-info">{item.release_date}</div>
      </div>

      <div className="section-div">
          <div className="genre title">Rating:</div>
          <div className="title-info">{item.vote_average}</div>
      </div>
      
      <div className="section-div">
          <div className="language title">Language:</div>
          <div className="title-info">{item.original_language}</div>
      </div>

       </div>

       <div className="right-two">
       <div className="plot-summary title">Plot Summary</div> 
           <div className="title-info">
                {item.overview}
          </div> 
       </div>
    </div>
    )
}



export default Section_two;