import React from 'react';
import style from './form.css'; 





const Form = () => {
    return (
    <div className="form">
       <form>
           <div className="name label">Name</div>
           <input type="text" />

           <div className="email label">E-mail</div>
           <input type="text" />

           <div className="password label">Password</div>
           <input type="password"/>
       </form>
       
    </div>
    )
}



export default Form;